.gpt4__navbar{
    display:flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 9rem;
}

.gpt4__navbar-links{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.gpt4__navbar-links_logo{
    margin-right: 2rem;
    color: white;
    font-size: 0.8rem;
}

.gpt4__navbar-links_logo img{
 width: 62.56px;
 height: 16.02px;   
}

.gpt4__navbar-links_container{
 display: flex;
 flex-direction: row; 
}

.gpt4__navbar-sign{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt4__navbar-links_container p,
.gpt4__navbar-sign p,
.gpt4__navbar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.gpt4__navbar-sign button,
.gpt4__navbar-menu_container button{
    padding: 0.5rem 1rem;
    color:#fff;
    background: #FF4820;
    font-family: var( --font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
    
}

.gpt4__navbar-menu{
    margin-left: 1rem;

    display: none;
    position: relative;
    color: #fff;
    display: flex;
    flex-direction: column;
}

.gpt4__navbar-menu svg{
    cursor: pointer;
}

.gpt4__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0px;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);

    }

    .gpt4__navbar-menu_container p{
        margin: 1rem 0;
    }

    .gpt4__navbar-menu_container-links-sign{
        display: none;
    }

    @media screen and (max-width: 1050px) {
        .gpt4__navbar-links_container{
            display: none;
        }

        .gpt4__navbar-menu{
            display: flex;
        }
        
    }

    @media screen and (max-width: 700px) {
        .gpt4__navbar{
            padding: 2rem 4rem;
        }
    }
    
    
    @media screen and (max-width: 550px) {
        .gpt4__navbar{
            padding: 2rem ;
        }

        .gpt4__navbar-sign{
            display: none;
        }
    

        .gpt4__navbar-menu_container{
        top: 20px;
        }

        .gpt4__navbar-menu_container-links-sign{
        display: block;
        }
    }



    











